import { factorial } from "mathjs";
import drawResult from "@/misc/genericCalc.js";
import variables from "@/misc/variables.js";

export default {
  data() {
    return {
      param_N: variables.inputDefaultValue,
      param_Ni: variables.inputDefaultValue,
      result: variables.outputTip,
      classObj: {}
    };
  },
  methods: {
    compute() {
      drawResult(this, this.perestanovkiRep, [
        +this.param_N,
        this.param_Ni.split(",").map(x => +x)
      ]);
    },
    perestanovkiRep(n, ni) {
      if (n !== ni.reduce((acc, item) => acc + item)) {
        throw new Error("n != sum(ni)");
      }
      let den = ni.reduce((acc, item) => acc * factorial(item), 1);
      return factorial(n) / den;
    }
  }
};
